import React from "react"
const CartList = (item:CartList) => {
    return(
<div 
    className="xl:m-2 xl:p-2 m-[0.25rem] p-[0.25rem] rounded-xl shadow-lg shadow-slate-400 border-[2px] xl:w-[30vw] w-[40vw] flex border-slate-200">
    <img src={`https://image.tmdb.org/t/p/w500/${item.poster_path}`} alt=""
    className="xl:h-32 h-24  rounded-2xl p-3"/>   
    <div className="relative">
    <div className="pt-2 p-2 font-bold xl:text-lg text-xs border-b-2 border-slate-300">{item.original_title}</div>
    <ul className="font-bold flex flex-wrap xl:text-lg text-xs">{item.genres.map((genre,index)=>index<=1?<li key={genre.id} className="xl:w-32 w-16 ">{genre.name}</li>:"")}</ul>
    <button 
    className="absolute xl:top-[-0.37rem] top-[-0.12rem] xl:right-[-0.7rem] right-[-0.1rem]  bg-red-400 text-white rounded-full w-6 p-1"
    onClick={()=>{item.setCartList(state=> state.filter(removeItem=>removeItem.id!==item.id))}}
    >
<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={5} stroke="currentColor" className="w-4 h-4">
  <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12"/>
</svg>
    </button>
    </div>
    </div>

    )
}
export default React.memo(CartList)